import { Job } from "../../types";
import { Space, Tooltip, Typography } from "antd";
import React from "react";
import { BarsOutlined, CalendarOutlined, DashboardOutlined, HistoryOutlined } from "@ant-design/icons";
import { generateDateInfo, generateTimeInfo } from "components/utils/utils";
import dayjs from "dayjs";
var numeral = require('numeral');

export default function JobDateTime({ job }: { job: Job; }) {
  return (
    <Space direction="horizontal" size="middle">
      <Space>
        <Typography.Text type="secondary" style={{ fontSize: 12 }}>
          <CalendarOutlined style={{ color: "inherit", fontSize: "inherit" }} />
        </Typography.Text>
        <Typography.Text type="secondary" style={{ fontSize: 12 }}>
          <Tooltip title={dayjs(job?.startTime).toString()}>
            {generateDateInfo(job)}
          </Tooltip>
        </Typography.Text>
      </Space>
      <Space>
        <Typography.Text type="secondary" style={{ fontSize: 12 }}>
          <HistoryOutlined style={{ color: "inherit", fontSize: "inherit" }} />
        </Typography.Text>
        <Typography.Text type="secondary" style={{ fontSize: 12 }}>
          {generateTimeInfo(job)}
        </Typography.Text>
      </Space>
      {job?.processId !== 0 && <Tooltip title="Process ID"><BarsOutlined /> <Typography.Text type="secondary" style={{ fontSize: 12 }}>{job?.processId}</Typography.Text></Tooltip>}
      {job?.memoryBytes !== 0 && <Tooltip title="Average Memory Usage"><DashboardOutlined /> <Typography.Text type="secondary" style={{ fontSize: 12 }}>{numeral(job?.memoryBytes).format('0.00 b')}</Typography.Text></Tooltip>}
    </Space>
  );
}
